import React from "react"
import { Link } from "gatsby"
import "src/scss/header.scss"

class Header extends React.Component {
  componentDidMount() {
    if (typeof window === "object" && "$" in window) {
      let $ = window.$
      $("[secretmailto]").each(function () {
        $(this).get(0).innerHTML = $(this).attr("secretmailto")
        $(this).attr("href", "mailto:" + $(this).attr("secretmailto"))
      })

      $("[secrettel]").each(function () {
        $(this).get(0).innerHTML = $(this).attr("secrettel")
        $(this).attr(
          "href",
          "tel:" + $(this).attr("secrettel").replace(/./g, "")
        )
      })
    }
  }
  render() {
    return (
      <>
        <header>
          <div>
            <span className="h-left">
              <a href="javascript:$('html, body').animate({scrollTop: 100000}, 2000);">
                <span className="uui-icon icon-happy" /> Paul Shorey
              </a>{" "}
              | Javascript Software Engineer / UI Designer / Tinkerer
            </span>
            <span className="h-right">
              <span className="uui-text">
                <a href="" target="_blank" secretmailto="paul@nlp.studio">
                  hidden
                </a>{" "}
                |
                <a href="" secrettel="385.770.6789">
                  hidden
                </a>
              </span>
            </span>
          </div>
          {/*<div>*/}
          {/*  <span className="h-left">*/}
          {/*    Read about: <Link to="/about">me + my hobbies</Link> |*/}
          {/*    <Link to="/about">my work + ambitions</Link> |*/}
          {/*    <Link to="/about">technical articles + ideas</Link>*/}
          {/*  </span>*/}
          {/*  <span className="h-right">*/}
          {/*    Featured article:{" "}*/}
          {/*    <Link to="/about">how to publish web apps (the easy way)</Link>*/}
          {/*  </span>*/}
          {/*</div>*/}
        </header>
      </>
    )
  }
}

export default Header
